import React from 'react';
import CodeMirror from '@uiw/react-codemirror';
import { html } from '@codemirror/lang-html';
import { css } from '@codemirror/lang-css';
import { javascript } from '@codemirror/lang-javascript';
import { Box, Typography, IconButton } from '@mui/material';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';

export const CodeEditor = ({ code, setCode, showCodeEditor, setShowCodeEditor }) => {
  return (
    <Box sx={{ flex: 1, border: '1px solid #ccc', borderRadius: '4px', overflow: 'hidden', height: '600px' }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', borderBottom: '1px solid #ccc', pb: 1 }}>
        <Typography variant="h6" component="h2" gutterBottom sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', flex: 1 }}>
          Code
        </Typography>
        <IconButton onClick={() => setShowCodeEditor(false)} sx={{ color: 'primary.main' }}>
          <VisibilityOffIcon />
        </IconButton>
      </Box>
      <Box sx={{ height: 'calc(100% - 48px)', overflowY: 'auto' }}>
        <CodeMirror
          value={code}
          height="100%"
          extensions={[html(), css(), javascript()]}
          onChange={(value) => setCode(value)}
          style={{ textAlign: 'left' }}
        />
      </Box>
    </Box>
  );
};